export default {
	name: 'picker-field',
	props: {
		label: {
			type: String
		},
		placeholder: {
			type: String,
			default: ''
		},
		value: {
			type: [String, Number]
		},
		textKey: {
			type: String,
			default: 'displayText'
		},
		valueKey: {
			type: String,
			default: 'value'
		},
		loading: {
			type: Boolean,
			default: false
		},
		columns: {
			type: Array,
			default() {
				return [];
			}
		},
		isCell: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			selectValue: undefined
		};
	},
	watch: {
		value: {
			handler(newVal) {
				if (newVal) {
					let i;
					for(i=0; i< this.columns.length; i++){
						if(this.columns[i].value == newVal){
							break;
						}
					}
					this.selectValue = i;
				}
			},
			immediate: true
		}
	},
	computed: {
		currentText() {
			if (this.value) {
				const currentItem = this.columns.filter(c => c[this.valueKey] == this.value)[0];
				if (currentItem) {
					return currentItem[this.textKey];
				}
			}
			return '';
		}
	},
	methods: {
		onFocus() {
		},
		onClick() {
			this.$refs.pickerPopup.open();
		},
		onCancel() {
			this.$refs.pickerPopup.close();
		},
		onConfirm(value) {
			this.$emit('input', value[this.valueKey]);
			this.$refs.pickerPopup.close();
		},
		pickerChange(event) {
			this.selectValue = event.detail.value;
			this.$emit('input', this.columns[event.detail.value].value);
		}
	}
};
